@charset "UTF-8";
@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

*[class*=title],
*[class*=heading],
*[class*=text] {
  margin: 0;
}

*[class*=link] {
  color: inherit;
  text-decoration: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.block-center {
  width: calc(100% - 40px);
  max-width: 280px;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .block-center {
    width: calc(100% - 60px);
    max-width: 708px;
  }
}
@media (min-width: 1190px) {
  .block-center {
    width: calc(100% - 200px);
    max-width: 1220px;
  }
}

.button {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  text-decoration: none;
  color: #ffffff;
  background-color: #68b738;
  text-align: center;
  border: none;
}
.button--more {
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .button {
    font-size: 20px;
    line-height: 20px;
  }
}

.page--column {
  height: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  color: #000000;
  background-color: #ffffff;
  font-size: 14px;
  line-height: 18px;
}
.page__body--column {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.page__header {
  padding: 10px 0 0 0;
}

.page__content--column {
  flex-grow: 1;
}

.page__title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.page__footer {
  padding: 39px 0 22px;
  color: #444444;
  background-color: #f2f2f2;
}

.page-header {
  font-family: "Oswald", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .page-header {
    margin-bottom: 35px;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (min-width: 1190px) {
  .page-header {
    margin-bottom: 32px;
    font-size: 20px;
    line-height: 30px;
  }
}

.page-header__nav {
  margin: 0;
  padding-bottom: 2px;
}

.page-header__bar {
  display: grid;
  grid-template-columns: 34px 1fr 34px;
  align-items: center;
  margin-bottom: 19px;
}
@media (min-width: 768px) {
  .page-header__bar {
    grid-template-columns: 174px;
    margin-bottom: unset;
  }
}
@media (min-width: 768px) {
  .page-header__bar {
    grid-template-columns: 202px;
  }
}

.page-header__logo {
  grid-column: 1/3;
  margin: 0;
}
@media (min-width: 768px) {
  .page-header__logo {
    grid-column: 1/-1;
  }
}

.title {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
}

.sub-title {
