  flex-direction: column;
  align-items: center;
  width: 100%;
  padding: 39px 0 22px 0;
}

.page-footer__logo {
  margin-bottom: 32px;
}

.page-footer__menu {
  margin-bottom: 22px;
}

.page-footer__link {
  display: flex;
  text-decoration: none;
}

.socials {
  display: flex;
  align-items: center;
