}

.socials__link {
  display: block;
  outline: 1px solid #a21c32;
  outline-offset: -1px;
}
.socials__link--vk {
  width: 23px;
  height: 16px;
}
.socials__link--instagramm {
  width: 23px;
  height: 16px;
}
.socials__link--facebook {
  width: 23px;
  height: 16px;
}

.designers {
  display: flex;
  justify-content: space-between;
