}

.logo {
  display: grid;
  grid-template-columns: 34px 1fr;
  align-items: center;
}

.logo__title {
  justify-self: center;
}

.form {
  display: flex;
  flex-direction: column;
}

.form__group {
  display: flex;
  flex-direction: column;
}
.form__group--additional {
  margin-bottom: 34px;
  color: #000000;
}
.form__group--contacts {
  color: #444444;
}

.form__subgroup {
  display: flex;
  flex-direction: column;
}
.form__subgroup--cat {
  color: #444444;
}
.form__subgroup--purpose {
