}

.form__label {
  display: flex;
  flex-direction: column;
  text-transform: uppercase;
}

.form__input {
  font: inherit;
  text-transform: inherit;
  color: inherit;
}

.form__group-legend {
  margin-bottom: 34px;
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 26px;
  text-transform: none;
  color: #000000;
}

.form__textarea {
  margin-top: 4px;
  font: inherit;
  text-transform: uppercase;
  color: #444444;
}

.form__submit {
  margin-bottom: 21px;
}

.form__note {
  margin: 0;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}

.control {
  padding-left: 45px;
}
.control::before {
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  content: "";
}
.control::after {
  width: 14px;
  height: 14px;
  background-color: #68b738;
  border: 1px solid #cdcdcd;
  content: "";
}
.control--radio::before {
  border-radius: 50%;
}
.control--radio::after {
  border-radius: 50%;
}
.control--checkbox::after {
  width: 2px;
  height: 8px;
}

.control__input {
  display: none;
}

.control__marker {
  font-size: 16px;
  line-height: 18px;
}

.togglers {
  display: flex;
}

.togglers__button {
  font-family: "Oswald", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
}
@media (min-width: 768px) {
