  .process__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    column-gap: initial;
  }
}

.process__item {
  display: flex;
}
.process__item:not(:last-of-type) {
  margin-bottom: 20px;
}
.process__item::before {
  flex-shrink: 0;
  display: block;
  width: 60px;
  height: 60px;
  margin-right: 20px;
  color: #ffffff;
  background-color: #68b738;
  background-repeat: no-repeat;
  background-size: 30px 32px;
  background-position: center;
  content: "";
}
.process__item--useful::before {
  background-image: url("../img/index/mobile/icon-useful.svg");
}
.process__item--powder::before {
  background-image: url("../img/index/mobile/icon-powder.svg");
}
.process__item--meal::before {
  background-image: url("../img/index/mobile/icon-meal.svg");
}
.process__item--results::before {
  background-image: url("../img/index/mobile/icon-results.svg");
}
@media (min-width: 768px) {
  .process__item {
    flex-direction: column;
    max-width: 280px;
  }
}

.process__text {
  align-self: center;
}
@media (min-width: 768px) {
  .process__text {
    font-size: 16px;
    line-height: 24px;
  }
}

