//Блок центровщик для размещения элементов по центру страницы
// с заданной шириной
.block-center {
  width: calc(100% - 20px * 2);
  max-width: 280px;
  margin: 0 auto;

  @media (min-width: $min-tablet) {
    width: calc(100% - 30px * 2);
    max-width: 708px;
  }

  @media (min-width: $min-desktop) {
    width: calc(100% - 100px * 2);
    max-width: 1220px;
  }
}
